html {
  font-size: 62.5%;
}

body {
  color: #003727;
  font-family: "Lato";
  font-weight: 300;
  font-size: 1.8rem;
  line-height: 1.4444444444;
  letter-spacing: normal;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Playfair Display";
  font-weight: 300;
}

h1 {
  font-size: 7.5rem;
  line-height: 1.1333333333;
  letter-spacing: normal;
}

h2 {
  font-size: 4rem;
  line-height: normal;
  letter-spacing: normal;
}

h3 {
  font-size: 2.8rem;
  line-height: normal;
  letter-spacing: normal;
}

h4 {
  font-size: 2.2rem;
  line-height: normal;
  letter-spacing: normal;
}

h5 {
  font-size: 1.8rem;
  line-height: normal;
  letter-spacing: normal;
}

a {
  color: #5B9581;
  text-decoration: none;
}

.bold {
  font-weight: bold;
}

.bolder, .copy__head p {
  font-weight: 400;
}

small {
  font-size: 1.4rem;
  line-height: normal;
  letter-spacing: normal;
}

strong {
  font-weight: 500;
}

.section--wide {
  padding: 5rem 2.5rem;
}
@media (min-width: 960px) {
  .section--wide {
    padding: 7.5rem 4.5rem;
  }
}
.section__content[bp~=grid] {
  row-gap: 7.5rem;
  column-gap: 6rem;
}
.section__copy[bp~=grid] {
  row-gap: 0.8rem;
}

.copy__icon {
  position: relative;
}
.copy__icon svg {
  width: 4rem;
  height: 4rem;
  fill: #003727;
}
.copy__icon::after {
  content: "";
  background-color: #C2FFE8;
  border-radius: 3px;
  height: 100%;
  position: absolute;
  top: -5px;
  left: 25px;
  width: 2.5rem;
  z-index: -1;
}
.copy__head h2 {
  margin-bottom: 1rem;
}
.copy__body p {
  margin-bottom: 1.2rem;
}
.copy__body p:last-of-type {
  margin-bottom: 0;
}
.copy__body p + ul {
  margin-top: 1.2rem;
}
.copy__body ul {
  margin-bottom: 1.2rem;
}
.copy__body ul:last-of-type {
  margin-bottom: 0;
}
.copy__body ul + p {
  margin-top: 1.2rem;
}
.copy__footer {
  margin-top: 1rem;
}

figure img {
  display: block;
  width: 100%;
  height: auto;
}

a.skip-link {
  left: -99rem;
  position: absolute;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
  z-index: -999;
}

.btn {
  border-radius: 0.2rem;
  display: inline-block;
  padding: 1.5rem 3rem;
  text-decoration: none;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: normal;
  letter-spacing: normal;
}
.btn--primary {
  color: #fff;
  background-color: #ea847a;
}
.btn--secondary {
  color: #003727;
  background-color: #fff;
}

.link {
  align-items: flex-end;
  display: inline-flex;
  font-weight: 500;
}
.link--secondary {
  color: #fff;
}

.icon-arrow {
  height: 1.9rem;
  width: 1.9rem;
  margin-left: 0.5rem;
}

.cta__inner {
  border-radius: 3px;
  background-color: #5B9581;
  padding: 5rem 3.5rem;
}
@media (min-width: 960px) {
  .cta__inner {
    padding: 7.5rem 3.5rem;
  }
}
.cta__content {
  color: #fff;
}
.cta__content--secondary {
  color: #fff;
  font-size: 1.6rem;
  line-height: 1.375;
  letter-spacing: 0.0125em;
}
.cta .copy__icon svg {
  height: 3rem;
  width: 3rem;
  fill: #fff;
}

.clients__media img {
  height: auto;
  width: 100%;
}

.header {
  background-color: rgba(255, 255, 255, 0.7);
  left: 0;
  height: 6rem;
  position: fixed;
  top: 0;
  transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
  width: 100%;
  z-index: 1;
}
.header::after {
  background: #C2FFE8;
  background: radial-gradient(circle, rgb(255, 255, 255) 6%, rgb(202, 253, 234) 23%, rgb(255, 255, 255) 59%);
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0.1rem;
}

.navbar {
  line-height: 6rem;
  padding: 0 2.5rem;
}
@media (min-width: 960px) {
  .navbar {
    padding: 0 4.5rem;
  }
}
.navbar__logo a {
  display: block;
  width: 9.5rem;
}
.navbar__logo img {
  display: block;
  height: auto;
  width: 100%;
}

.sUp .header {
  transform: translateY(0);
}

.sDown .header {
  transform: translateY(-6rem);
}

#gradient-canvas {
  width: 100%;
  height: 100%;
  --gradient-color-1: #c2ffe8;
  --gradient-color-2: #ffffff;
  --gradient-color-3: #e0fff5;
  --gradient-color-4: #ffffff;
}

.hero {
  position: relative;
  width: 100%;
  height: auto;
  padding: 10rem 2.5rem;
  margin-bottom: 5rem;
}
@media (min-width: 960px) {
  .hero {
    padding-left: 4.5rem;
    padding-right: 4.5rem;
    height: 100vh;
  }
}
.hero__bg-gradient {
  left: 0;
  position: absolute;
  top: 0;
  z-index: -2;
}
.hero .section--wrapper {
  height: 100%;
}
.hero__content {
  height: 100%;
}
.hero__copy {
  align-items: center;
}
.hero__copy h1 {
  margin-bottom: 2.5rem;
}

.page-hero {
  background: linear-gradient(81deg, rgb(224, 255, 245) 0%, rgb(255, 255, 255) 73%, rgb(255, 255, 255) 100%);
  padding-top: 10rem;
  position: relative;
}
.page-hero--media {
  background: none;
  padding-bottom: 0;
  margin-bottom: 4rem;
}
.page-hero__head h1 {
  font-size: 4.9rem;
  line-height: normal;
  letter-spacing: normal;
}
.page-hero__cta--group {
  column-gap: 2.5rem;
  display: block;
}
@media (min-width: 720px) {
  .page-hero__cta--group {
    display: flex;
  }
}
.page-hero__cta--group-btn {
  text-align: center;
  margin-bottom: 2.5rem;
}
@media (min-width: 720px) {
  .page-hero__cta--group-btn {
    text-align: right;
    margin-bottom: 0;
  }
}
.page-hero__cta--group-link {
  text-align: center;
